// Types
export type ProblemCategory = 
  | 'impressora'
  | 'scanner'
  | 'rede'
  | 'cabo'
  | 'login'
  | 'acesso'
  | 'hardware';

export type Sector = 
  | 'administracao'
  | 'recepcao'
  | 'comunicacao'
  | 'pesquisa'
  | 'outros';

export interface Ticket {
  id: string;
  problema: ProblemCategory;
  setor: Sector;
  descricao: string;
  status: 'aberto' | 'em_andamento' | 'fechado';
  prioridade: 'baixa' | 'media' | 'alta' | 'urgente';
  justificativaPrioridade?: string;
  dataCriacao: string;
  email: string;
  nome: string;
}

// Dummy data
const dummyTickets: Ticket[] = [
  {
    id: "1",
    problema: "impressora",
    setor: "administracao",
    descricao: "Impressora não está funcionando corretamente",
    status: "aberto",
    prioridade: "alta",
    dataCriacao: new Date().toISOString(),
    email: "usuario@imazon.org.br",
    nome: "João Silva"
  },
  {
    id: "2",
    problema: "rede",
    setor: "pesquisa",
    descricao: "Problemas com conexão de rede",
    status: "em_andamento",
    prioridade: "media",
    dataCriacao: new Date().toISOString(),
    email: "maria@imazon.org.br",
    nome: "Maria Santos"
  }
];

// API functions
export const fetchTickets = async (): Promise<Ticket[]> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(dummyTickets);
    }, 1000);
  });
};

export const fetchTicketDetails = async (ticketId: string): Promise<Ticket | null> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      const ticket = dummyTickets.find(t => t.id === ticketId);
      resolve(ticket || null);
    }, 1000);
  });
};

export const submitTicket = async (ticketData: Omit<Ticket, 'id' | 'dataCriacao' | 'status'>): Promise<Ticket> => {
  return new Promise((resolve) => {
    setTimeout(() => {
      const newTicket: Ticket = {
        ...ticketData,
        id: Math.random().toString(36).substr(2, 9),
        dataCriacao: new Date().toISOString(),
        status: 'aberto'
      };
      dummyTickets.push(newTicket);
      resolve(newTicket);
    }, 1000);
  });
};

