"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { fetchTicketDetails, type Ticket, PROBLEM_CATEGORIES, SECTORS } from "@/lib/api"
import Link from "next/link"

export default function AdminTicketDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const [ticket, setTicket] = useState<Ticket | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const loadTicket = async () => {
      try {
        if (typeof params.id !== 'string') {
          throw new Error('Invalid ticket ID')
        }
        const data = await fetchTicketDetails(params.id)
        if (!data) {
          throw new Error('Ticket not found')
        }
        setTicket(data)
      } catch (error) {
        console.error("Error loading ticket:", error)
        router.push('/admin/tickets')
      } finally {
        setLoading(false)
      }
    }

    loadTicket()
  }, [params.id, router])

  const getStatusColor = (status: string) => {
    switch (status) {
      case "aberto":
        return "bg-green-100 text-green-800"
      case "em_andamento":
        return "bg-yellow-100 text-yellow-800"
      case "fechado":
        return "bg-gray-100 text-gray-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getPriorityColor = (prioridade: string) => {
    switch (prioridade) {
      case "alta":
        return "bg-red-100 text-red-800"
      case "media":
        return "bg-orange-100 text-orange-800"
      case "baixa":
        return "bg-blue-100 text-blue-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  const getProblemLabel = (problemValue: string) => {
    return PROBLEM_CATEGORIES.find(cat => cat.value === problemValue)?.label || problemValue
  }

  const getSectorLabel = (sectorValue: string) => {
    return SECTORS.find(sec => sec.value === sectorValue)?.label || sectorValue
  }

  if (loading) {
    return (
    <div className="min-h-screen p-8 bg-background">
      <div className="max-w-3xl mx-auto text-center py-8 text-foreground">
          Carregando detalhes do chamado...
        </div>
      </div>
    )
  }

  if (!ticket) {
    return (
      <div className="min-h-screen p-8 bg-background">
        <div className="max-w-3xl mx-auto text-center py-8 text-foreground">
          Chamado não encontrado.
          <div className="mt-4">
            <Link href="/admin/tickets">
              <Button>Voltar para Lista</Button>
            </Link>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen p-8 bg-background">
      <div className="max-w-3xl mx-auto">
        <Card className="bg-card border-border">
          <CardHeader>
            <div className="flex justify-between items-center">
              <CardTitle>Detalhes do Chamado</CardTitle>
              <Link href="/admin/tickets">
                <Button variant="outline">Voltar</Button>
              </Link>
            </div>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="flex justify-between items-start">
              <div>
                <h2 className="text-2xl font-bold mb-2">{getProblemLabel(ticket.problema)}</h2>
                <div className="flex gap-2 items-center text-sm text-gray-600">
                  <span>Por: {ticket.nome}</span>
                  <span>•</span>
                  <span>{ticket.email}</span>
                </div>
                <div className="mt-2 text-sm text-gray-600">
                  <span>Setor: {getSectorLabel(ticket.setor)}</span>
                </div>
              </div>
              <div className="flex flex-col gap-2">
                <span className={`px-3 py-1 rounded-full text-sm ${getStatusColor(ticket.status)}`}>
                  {ticket.status.replace('_', ' ')}
                </span>
                <span className={`px-3 py-1 rounded-full text-sm ${getPriorityColor(ticket.prioridade)}`}>
                  {ticket.prioridade}
                </span>
              </div>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Descrição</h3>
              <p className="text-gray-700 whitespace-pre-wrap">{ticket.descricao}</p>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Informações Adicionais</h3>
              <div className="bg-gray-50 p-4 rounded-lg">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-gray-600">Data de Criação</p>
                    <p className="font-medium">
                      {new Date(ticket.dataCriacao).toLocaleDateString('pt-BR', {
                        day: '2-digit',
                        month: '2-digit',
                        year: 'numeric',
                        hour: '2-digit',
                        minute: '2-digit'
                      })}
                    </p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">ID do Chamado</p>
                    <p className="font-medium">{ticket.id}</p>
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
